import { Quote } from "../../modules/quotes_list/quotes_list.modules";
import { AppThunk } from "../store";

export const ADD_QUOTE_TO_FAVORITES = "ADD_QUOTE_TO_FAVORITES";
export const REMOVE_QUOTE_FROM_FAVORITES = "REMOVE_QUOTE_FROM_FAVORITES";
export const GET_FAVORITES_LIST_REQUEST = "GET_FAVORITES_LIST_REQUEST";

export const addQuoteToFavoritesList = (quoteToAdd: Quote): any => {
    return {
        type: ADD_QUOTE_TO_FAVORITES,
        quoteToAdd
    }
}

export const removeFromFavoritesList = (quoteToRemove: Quote): any => {
    return {
        type: REMOVE_QUOTE_FROM_FAVORITES,
        quoteToRemove
    }
}

export interface FavoritesListSuccessAction  {
    type: string,
    response: Quote[]
}
export const getFavoritesListSuccessAction = (response: Quote[] = []): FavoritesListSuccessAction  =>  ({
    type: GET_FAVORITES_LIST_REQUEST,
    response
})


export const manageFavoritesList = (quote: Quote): AppThunk => (
    dispatch, 
): Quote[] | FavoritesListSuccessAction => {
    dispatch(addQuoteToFavoritesList(quote));

    const favoritesList: Quote[] = [];

    favoritesList.map((favoriteQuote, index) => {
        if(favoriteQuote.quote !== quote.quote) {
            return favoritesList.push(quote);
        }
        if(favoriteQuote.quote === quote.quote) {
            dispatch(removeFromFavoritesList(quote));
            return favoritesList.splice(index, 1);
        }
    });
    
    return favoritesList;
}
